.series {
    padding-top: 20px;

    &__list {
        margin: 0;
        padding: 0;
        list-style: none;

        &-item {
            margin-bottom: 10px;
            text-transform: capitalize;

            .subtitle {
                background-color: var(--red);
                border-radius: 2px;
                padding: 0 8px;
            }

            .colon {
                width: 20px;
                font-weight: var(--secondary-font-weight);
                text-align: center;
            }

            .description{
                text-shadow: var(--text-shadow);
            }
        }
    }
}